        const geometry = new THREE.BoxGeometry( 255, 255, 255 );
        const wireframe = new THREE.EdgesGeometry( geometry );
        const line = new THREE.LineSegments( wireframe );
        line.position.set(255 * 0.5, 255 * 0.5, 255 * 0.5)
        // @ts-expect-error
        line.material.color.r = 0.5; line.material.color.g = 0.5; line.material.color.b = 0.5;
        // @ts-expect-error
